* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
ul, ol, li {
	list-style: none;
}

body {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #dac4a7bd;
	color: #444444;
	line-height: 1.5;
	font-size: 20px;
}
.find-section {
	max-width: 1000px;
	margin: 0 auto;
	padding-top: 100px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.find-text {
	font-size: 40px;
	margin-bottom: 40px;
}
#search-box {
	font: inherit;
	padding: 8px;
	max-width: 500px;
	width: 100%;
	border-radius: 10px;
	border: 1px solid #444444;
	outline: none;
	margin-bottom: 20px;
}

.country-list {
	display: flex;
	flex-direction: column;
	max-width: 500px;
	width: 100%;
}
.country__item {
	display: flex;
	align-items: center;
	gap: 20px;
	padding: 20px;
}
.country__flag {
	display: block;
	height: auto;
	max-width: 50px;
}
.country__name {
	font-size: 64px;
}
.country__list-info {
	padding: 0 20px;
}
.bold-text {
	font-weight: bold;
}